.search-wrapper {
  width: 100%;
  height: auto;
  margin-top: 15vh;
  position: relative;
}
.search {
  padding: 10px;
  border: 3px solid rgb(2, 13, 16);
  background: #233a50;
  width: 100%;
}
.search::placeholder {
  color: grey;
  font-size: 1.2rem;
}
.search:focus {
  outline: none;
  caret-color: white;
  color: white;
}
.search-icon {
  position: absolute;
  right: 0.8rem;
  top: 30%;
  width: 1.5rem;
  height: 1.2rem;
  fill: white;
}
.books-block {
  margin-top: 10vh;
  padding-bottom: 10vh;
}
.book-slider {
  padding-top: 5vh;
}
.book-card {
  margin-right: 1.2rem;
  cursor: pointer;
}
.book-image {
  width: 100%;
  height: 80%;
  border-radius: 10px;
}
.book-name {
  padding: 5px 5px 0 5px;
  font-size: 1.2rem;
  color: #ffffff;
  margin: 0;
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.book-list-cat {
  font-weight: 300;
  color: grey;
  font-size: 0.7rem;
}
