.catalog-2 {
  width: 100%;
  min-height: 25vh;
  position: relative;
  display: flex;
  justify-content: center;
}
.line {
  top: 50px;
  width: 100%;
  position: absolute;
  border: 1px solid rgba(255, 255, 255, 0.04);
}
.catalog-2-main-block {
  width: 70%;
  min-height: 50vh;
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.black-bg {
  background: #121212;
  color: white;
}
.white-bg {
  background: white;
  color: black;
}
.new-compositions {
  margin-bottom: 5rem;
}
.new-compos {
  cursor: pointer;
  display: flex;
  padding: 4px;
  width: 100%;
  border-radius: 5px;
  margin-bottom: 0.5rem;
}
.compos-img-wrapper {
  width: 15%;
}
.compos-info {
  padding-top: 10px;
}
.comspos-img {
  max-width: 90%;
  max-height: 100%;
  border-radius: 5px;
}
@media (max-width: 400px) {
  .compos-info {
    padding-top: 0px;
  }
}
.limit-line {
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 3px;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.pop-compos {
  cursor: pointer;
  display: flex;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 0.5rem;
  position: relative;
}
.pop-img-wrapper {
  width: 103px;
  height: 46px;
}
.pop-img {
  max-width: 100%;
  max-height: 100%;
  z-index: 1;
}
.pop-info {
  position: absolute;
  left: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 3px;
  display: -webkit-box;
  -webkit-line-clamp: 2; /* number of lines to show */
  -webkit-box-orient: vertical;
}

.bottom-line {
  width: 75%;
  border: 1px solid rgba(255, 255, 255, 0.04);
}
@media only screen and (max-width: 950px) {
  .catalog-2-main-block {
    width: 90%;
    min-height: 50vh;
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}
