.wrapper {
  width: 100%;
  height: 100%;
}
.bg-im {
  background-image: url("./bg-im.jpg");
  min-height: 100vh;
  display: flex;
  justify-content: center;
}
.custom-container {
  width: 70%;
  position: relative;
}
.my-nav {
  padding: 0 15% 0 15%;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 2fr;
  height: 10vh;
  z-index: 10;
  position: fixed;
  background: #121212;
  border-bottom: 1px solid rgba(255, 255, 255, 0.04);
}
.main-nav-part {
  display: flex;
  height: 100%;
  align-items: center;
}
.right-side-nav {
  display: flex;
  justify-content: flex-end;
  font-size: 1rem;
  align-items: center;
  height: 100%;
}
.profileIcon {
  width: 2rem;
  height: 100%;
  color: #ffffff;
  cursor: pointer;
}
.colorChanger {
  margin-right: 1rem;
  width: 1.5rem;
  height: 100%;
  fill: white;
  cursor: pointer;
}
.title-brand {
  padding-right: 10px;
  color: #ffffff;
  cursor: pointer;
  text-decoration: none;
}
.cat-link {
  padding: 5px;
  font-size: 1rem;
  color: #ffffff;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.cat-icon {
  width: 1rem;
  height: 1rem;
  color: #ffffff;
  margin-right: 0.1rem;
}
@media only screen and (max-width: 625px) {
  .custom-container {
    width: 90%;
  }
  .my-nav {
    padding: 0 5% 0 5%;
  }
}
@media only screen and (max-width: 285px) {
  .custom-container {
    width: 100%;
  }
  .my-nav {
    padding: 0;
  }
  .wrapper {
    display: grid;
  }
}
