.filter-wrapper {
  padding-top: 15vh;
  min-height: 100vh;
  width: 100%;

  display: flex;
  justify-content: center;
}
.filters {
  padding-bottom: 5%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}
.filter-block {
  margin-top: 3rem;
}
.filter-input {
  width: 100%;
  padding: 0.5rem;
}
.input-black {
  background: #252525;
  border: 0;
  color: white;
}
.input-white {
  background: #dfdfdf;
  border: 0;
  color: black;
}
.filter-input::placeholder {
  font-size: 1rem;
}
.dropwdowns {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 5vh;
}
.filter-dropdown {
  position: relative;
  display: flex;
  justify-content: center;
  margin-right: 10px;
}
.filter-dropdown-button {
  border: 0;
  padding: 1vh 1vw 1vh 10px;
  font-size: 100%;
  border-radius: 5px;
  display: flex;
  align-items: center;
  position: relative;
  color: white;
}
.dropdown-icon {
  margin-top: 5%;
  margin-left: 5%;
  width: 30px;
}

.dropdown-list {
  visibility: hidden;
  width: 200%;
  max-height: 300px;
  overflow-y: scroll;
  box-shadow: 0 0 10px black;
  padding: 10px 5px 10px 10px;
  border-radius: 8px;
  opacity: 0;
  position: absolute;
  transition: all 0.5s ease;
  right: 0;
  top: 0;
  z-index: 10;
  display: none;
}
.dropdown-list.first {
  left: 0;
}
.dropdown-black {
  background: #191919;
  color: white;
}
.dropdown-white {
  background: whitesmoke;
  color: black;
}

.filter-dropdown:hover .dropdown-list {
  visibility: visible;
  opacity: 1;
  display: block;
}
.filter-choice {
  display: flex;
  align-items: center;
  font-size: 18px;
  padding-bottom: 5px;
}
.filter-choice-checkbox {
  margin-right: 10px;
  position: relative;
}

.book-list-filter {
  margin-top: 5vh;
}
.book-card-filter {
  width: 100%;
  padding: 5px;
  margin: 0;
  cursor: pointer;
}
.pagination {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}
.pagination-btn {
  width: 2rem;
  height: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.light-pagination {
  background: white;
  color: #00c5ff;
}
.dark-pagination {
  background: #191919;
  color: white;
}
.light-active {
  background: #00c5ff;
  color: white;
}
.dark-active {
  background: white;
  color: black;
}
.active-link {
  border: 0;
  outline: none;
}
.previous-btn > a:focus,
.previous-btn:focus,
.next-btn:focus,
.next-btn > a:focus {
  outline: none;
  border: 0;
  background: #00c5ff;
}

@media (min-width: 750px) {
  .dropwdowns {
    padding-left: 5%;
  }
  .previous-btn {
    margin-left: 5%;
  }
}
@media (min-width: 1350px) {
  .dropwdowns {
    padding-left: 3%;
  }
}
@media (max-width: 400px) {
  .filter-dropdown-button {
    padding: 5px 5px 5px 10px;
    font-size: 90%;
  }
  .dropdown-icon {
    margin-top: 3%;
    margin-left: 3%;
  }
}
@media (max-width: 320px) {
  .filter-dropdown-button {
    padding: 5px 5px 5px 10px;
    font-size: 90%;
  }
  .dropdown-icon {
    margin-top: 3%;
    margin-left: 3%;
  }
  .filter-dropdown {
    margin-right: 5px;
  }
}
