::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: grey;
}
.detail-wrapper {
  display: flex;
  padding-top: 15vh;
  padding-bottom: 10vh;
  min-height: 100vh;
  justify-content: center;
  position: relative;
  width: 100%;
}
.detail-container {
  width: 90%;
}
.detail-block {
  width: 100%;
  border-radius: 10px;
  padding: 10px;
  padding-bottom: 5vh;
  margin: 0 0 5vh 0;
}
.detail-block-bl {
  background: #191919;
  color: whitesmoke;
}
.detail-block-lt {
  background: #f8f8f8;
  color: black;
}
.detail-img {
  border-radius: 10px;
  max-width: 100%;
}
.header-display {
  display: flex;
  margin-bottom: 10px;
  align-items: center;
  overflow-x: scroll;
}
.icon-detail {
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.star {
  fill: #ffc107;
}
.heart:hover {
  fill: red;
  color: red;
}
.liked {
  color: red;
  fill: red;
}
.star-value {
  font-size: 100%;
  margin-left: 5px;
}
.eye {
  fill: grey;
}
.detail-header {
  width: 100%;
  padding: 10px 0 10px 0;

  white-space: nowrap;
  text-overflow: ellipsis;
}
.detail-genres {
  width: 90%;
  margin-bottom: 5vh;
  overflow-x: scroll;
  white-space: nowrap;
}
.description {
  height: 15vh;
  overflow-y: scroll;
}
.read {
  width: 100%;
  margin-top: 10px;
}
.btn-block {
  width: 100%;
}
.detail-main {
  width: 100%;
}
.detail-nav {
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.04);
  word-spacing: normal;
  overflow-x: scroll;
}
.detail-description {
  padding-left: 10px;
}
.nav-btn {
  cursor: pointer;
  padding: 10px 1.5rem 5px 10px;
  font-size: 1.8rem;
}
.active-btn {
  color: aqua;
}
.chapter-block {
  padding-top: 5vh;
}
.chapter-item {
  display: flex;
  width: 90%;
  padding-top: 10px;
  padding-left: 15px;
  padding-bottom: 10px;
  padding-right: 15px;
  border-radius: 10px;
  cursor: pointer;
  margin-bottom: 10px;
}
.chapter-item:hover {
  box-shadow: 0 0 10px black;
}

.pages-wrapper {
  width: 100%;
  opacity: 0.95;
  min-height: 50vh;
  background: black;
  display: flex;
  justify-content: center;
  padding-top: 15vh;
}
.set-star {
  display: flex;
  justify-content: space-between;
  padding: 10px 5px 10px 5px;
}
.set-comment-value {
  width: 100%;
}
.chosenStar {
  fill: red;
}
.review-item {
  display: flex;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  margin-bottom: 0.5rem;
}
.review-img-wrapper {
  width: 50px;
  height: 50px;
  position: relative;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 10px;
}
.review-img {
  display: inline;
  margin: 0 auto;
  height: 50px;
  width: 100%;
}
.review-text {
  width: 100%;
}

.rating {
  display: flex;
  font-size: 20px;
  line-height: 0.75;
  margin-bottom: 10px;
}
.rating__body {
  position: relative;
}
.rating__body::before {
  content: "★★★★★";
  display: block;
}
.rating__active {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  height: 100%;
}
.rating__active::before {
  content: "★★★★★";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #ffd300;
}
.rating__items {
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.rating__item {
  flex: 0 0 20%;
  height: 100%;
  opacity: 0;
}
.comment-title:hover {
  text-decoration: underline;
  color: aqua;
}
.center-nav-part {
  align-items: center;
  color: white;
  font-size: large;
  justify-content: center;
  display: flex;
}
.chapter-nav {
  display: flex;
  justify-content: center;
  align-items: center;

  padding-left: 10px;
  padding-right: 10px;
  width: 70%;
  height: 70%;

  position: relative;
}
.nav-title-ch {
  padding: 5px 10px 5px 10px;
}
.chapter-nav:hover .nav-title-ch {
  background: rgb(45, 45, 45);
  border-radius: 5px;
}
.chapter-arrow {
  font-size: 16px;
  cursor: pointer;
}
.left {
  margin-right: 10px;
  margin-left: 5px;
  font-size: 22px;
}
.right {
  font-size: 22px;
  margin-left: 10px;
  margin-right: 5px;
}
.back-arrow {
  width: 25px;
  height: 25px;
  fill: white;
  color: white;
}
.ch-dropdown {
  position: absolute;
  top: 100%;
  opacity: 0;
  visibility: hidden;
  max-height: 250px;
  min-width: 150px;
  border-radius: 10px;
  overflow-y: scroll;
  background: #181818;
}
.active-cp {
  width: 100%;
  height: 100%;
  color: aqua;
  font-weight: 700;
  margin: 0;
}
.ch-list {
  padding: 10px 0 10px;
}
.ch-list > li {
  white-space: nowrap;
  text-overflow: ellipsis;
  padding-bottom: 5px;
  max-width: 250px;
  overflow-x: hidden;
  padding-left: 10px;
  cursor: pointer;
}
.chapter-nav:hover .ch-dropdown {
  opacity: 1;
  visibility: visible;
}
@media only screen and (min-width: 600px) {
  .review-item {
    width: 80%;
  }
}
@media (max-width: 575px) {
  .detail-img {
    height: 100%;
    width: 100%;
  }
  .review-img {
    height: 40px;
  }
  .back-arrow {
    width: 15px;
    height: 25px;
  }

  .ch-list > li {
    max-width: 100px;
  }

  .left {
    top: 10%;
    right: 90%;
  }
  .right {
    top: 10%;
    left: 90%;
  }
}
