.form-wrapper {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding-top: 15vh;
}
.form-block {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.login-form {
  padding: 5vh;
  min-width: 30vw;
  border-radius: 10px;
}
.light-form {
  background: rgb(207, 207, 207);
}
.black-form {
  background: rgba(22, 22, 22, 0.8);
}
.black-input {
  color: white;
  background: rgba(30, 30, 30, 0.8);
  border: 0;
  outline: none;
}
.label {
  font-size: 18px;
  margin-bottom: 5px;
}
.form-title {
  width: 100%;
  padding-bottom: 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.custom-input:focus {
  box-shadow: 0 0 10px rgb(20, 20, 20);
}
.form-btn {
  width: 100%;
}

@media only screen and (max-width: 400px) {
  .login-form {
    width: 100%;
    padding: 5vh;
    min-width: 30vw;
    border-radius: 10px;
  }
}

.profile-wrapper {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding-top: 15vh;
}
.prof-nav {
  max-width: 100%;
  overflow-x: scroll;
}
.profile-img {
  max-width: 100%;
}
.popup-background {
  top: 0;
  left: 0;
  position: absolute;
  min-width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.9);
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 25vh;
}
.popup-form {
  padding: 20px;
  background: rgb(29, 29, 29);
  position: fixed;
  border-radius: 10px;
}
.close {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: yellow;
  width: 25px;
  height: 25px;
  top: -5%;
  right: 0%;
  position: absolute;
  color: aqua;
  cursor: pointer;
}
